import {put} from 'redux-saga/effects';
import axios from '../../axios-orders';
import * as actions from '../actions/index';

export function* purchaseBurgerSaga(action) {
  yield put(actions.purchaseBurgerStart());
  try {
    const response = axios.post( '/orders.json?auth=' + action.token, action.orderData );
    yield put(actions.purchaseBurgerSuccess(response.data.name, action.orderData ) )
  }
  catch(error){
    yield put(actions.purchaseBurgerFail(error))
  }
};

export function* fectchOrdersSaga(action) {
  yield put(actions.fetchOrdersStart());
  const queryParams = `?autrh=${action.token}&orderBy="userId"&equalTo="${action.userId}"`;
  try {
    const response = yield axios.get( '/orders.json?auth=' + queryParams);
    const fetchedOrders = [];
      for ( let key in response.data ) {
          fetchedOrders.push( {
              ...response.data[key],
              id: key
          } );
      }
    yield put(actions.fetchOrdersSuccess(fetchedOrders));
  }
  catch(error){
    yield put(actions.fetchOrdersFail(error));
  }
}


